import { Component, Prop, State, Event, EventEmitter, Element, h, Watch, AttachInternals } from '@stencil/core';
import { updateValidity } from '../../utils/form';

export type InputNumberState = 'placeholder' | 'filled' | 'hover' | 'focused';

@Component({
  tag: 'bh-input-number',
  styleUrl: 'bh-input-number.css',
  shadow: true,
  formAssociated: true,
})
export class BhInputNumber {
  @Element() el!: HTMLElement;
  @AttachInternals() internals!: ElementInternals;

  private inputEl?: HTMLInputElement;

  /**
   * Whether the input is in an error state
   */
  @Prop() error: boolean = false;

  /**
   * Whether the field is required
   */
  @Prop() required: boolean = false;

  /**
   * Whether the input is disabled
   */
  @Prop() disabled: boolean = false;

  /**
   * Whether to show the help icon
   */
  @Prop() showHelpIcon: boolean = false;

  /**
   * Whether to show the leading icon
   */
  @Prop() showLeadingIcon: boolean = false;

  /**
   * Whether to show the label
   */
  @Prop() showLabel: boolean = true;

  /**
   * Whether to show the hint text
   */
  @Prop() showHintText: boolean = true;

  /**
   * Label text for the input
   */
  @Prop() label: string = 'Amount';

  /**
   * Placeholder text
   */
  @Prop() placeholder: string = '0';

  /**
   * Hint text displayed below the input
   */
  @Prop() hintText: string = 'Enter a numeric value.';

  /**
   * Leading icon name (Material Symbols)
   */
  @Prop() leadingIcon: string = 'tag';

  /**
   * Tooltip text for the help icon
   */
  @Prop() helpIconTooltip: string = 'Help';

  /**
   * Current numeric value
   */
  @Prop({ mutable: true }) value: number | null = null;

  /**
   * Minimum allowed value
   */
  @Prop() min: number | null = null;

  /**
   * Maximum allowed value
   */
  @Prop() max: number | null = null;

  /**
   * Step value for increment/decrement
   */
  @Prop() step: number = 1;

  /**
   * Text prefix before the input value
   */
  @Prop() prefixText: string = '';

  /**
   * Text suffix after the input value
   */
  @Prop() suffixText: string = '';

  /**
   * Width of the component
   */
  @Prop() width: string = '';

  /**
   * Name attribute for form submission
   */
  @Prop() name: string = '';

  /**
   * Default value for form reset
   */
  @Prop() defaultValue: number | null = null;

  /**
   * Custom validation message
   */
  @Prop() validationMessage: string = '';

  @State() internalState: InputNumberState = 'placeholder';
  @State() isHovered: boolean = false;
  @State() isFocused: boolean = false;
  @State() internalValue: number | null = null;
  @State() hasValidationError: boolean = false;

  /**
   * Event emitted when the value changes
   */
  @Event() bhChange!: EventEmitter<number | null>;

  /**
   * Event emitted when the input receives focus
   */
  @Event() bhFocus!: EventEmitter<void>;

  /**
   * Event emitted when the input loses focus
   */
  @Event() bhBlur!: EventEmitter<void>;

  /**
   * Event emitted when the help icon is clicked
   */
  @Event() bhHelpClick!: EventEmitter<void>;

  private get isControlled(): boolean {
    return this.value !== undefined && this.value !== null;
  }

  private get currentValue(): number | null {
    return this.isControlled ? this.value : this.internalValue;
  }

  @Watch('value')
  watchValue(newValue: number | null) {
    this.hasValidationError = this.isOutOfBounds(newValue);
    this.updateInternalState(newValue);
  }

  componentWillLoad() {
    if (this.value !== null && this.value !== undefined) {
      this.internalValue = this.value;
    }
    this.hasValidationError = this.isOutOfBounds(this.currentValue);
    this.updateInternalState(this.currentValue);
  }

  componentDidLoad() {
    this.syncNumberFormValue();
    updateValidity(this.internals, this.inputEl, this.validationMessage);
  }

  formResetCallback() {
    this.value = this.defaultValue;
    this.internalValue = this.defaultValue;
    if (this.inputEl) {
      this.inputEl.value = this.defaultValue !== null ? String(this.defaultValue) : '';
    }
    this.hasValidationError = this.isOutOfBounds(this.defaultValue);
    this.updateInternalState(this.defaultValue);
    this.syncNumberFormValue();
    updateValidity(this.internals, this.inputEl);
  }

  private syncNumberFormValue() {
    if (!this.internals || !this.name) return;
    const val = this.currentValue;
    if (val === null || val === undefined) {
      this.internals.setFormValue(null);
    } else {
      this.internals.setFormValue(String(val));
    }
  }

  private isOutOfBounds(val: number | null): boolean {
    if (val === null) return false;
    if (this.min !== null && val < this.min) return true;
    if (this.max !== null && val > this.max) return true;
    return false;
  }

  private updateInternalState(val: number | null) {
    if (val !== null && val !== undefined) {
      this.internalState = 'filled';
    } else if (!this.isFocused && !this.isHovered) {
      this.internalState = 'placeholder';
    }
  }

  private validateValue(val: number): number {
    if (this.min !== null && val < this.min) {
      return this.min;
    }
    if (this.max !== null && val > this.max) {
      return this.max;
    }
    return val;
  }

  private updateValue(newValue: number | null, allowOutOfBounds = false) {
    let finalValue: number | null;

    if (newValue === null) {
      finalValue = null;
    } else if (allowOutOfBounds) {
      finalValue = newValue;
    } else {
      finalValue = this.validateValue(newValue);
    }

    this.internalValue = finalValue;
    this.value = finalValue;
    this.hasValidationError = this.isOutOfBounds(finalValue);
    this.syncNumberFormValue();
    updateValidity(this.internals, this.inputEl, this.validationMessage);
    this.bhChange.emit(finalValue);
  }

  private handleInputChange = (event: Event) => {
    const input = event.target as HTMLInputElement;
    const inputValue = input.value;
    const numericValue = inputValue === '' ? null : parseFloat(inputValue);

    if (numericValue !== null && !isNaN(numericValue)) {
      this.updateValue(numericValue, true);
    } else if (inputValue === '') {
      this.updateValue(null, true);
    }
  };

  private increment = () => {
    const baseValue = this.currentValue ?? 0;
    const newValue = baseValue + this.step;
    this.updateValue(newValue);
  };

  private decrement = () => {
    const baseValue = this.currentValue ?? 0;
    const newValue = baseValue - this.step;
    this.updateValue(newValue);
  };

  private handleInputFocus = () => {
    this.isFocused = true;
    this.internalState = 'focused';
    this.bhFocus.emit();
  };

  private handleInputBlur = () => {
    this.isFocused = false;
    this.updateInternalState(this.currentValue);
    this.bhBlur.emit();
  };

  private handleMouseEnter = () => {
    if (!this.isFocused) {
      this.isHovered = true;
      this.internalState = 'hover';
    }
  };

  private handleMouseLeave = () => {
    this.isHovered = false;
    if (!this.isFocused) {
      this.updateInternalState(this.currentValue);
    }
  };

  private handleHelpIconClick = () => {
    this.bhHelpClick.emit();
  };

  private getValidationErrorMessage(): string {
    if (!this.hasValidationError || this.currentValue === null) return '';

    if (this.min !== null && this.max !== null) {
      return `Enter a number between ${this.min} and ${this.max}.`;
    }

    if (this.min !== null && this.currentValue < this.min) {
      return `Value must be at least ${this.min}`;
    }

    if (this.max !== null && this.currentValue > this.max) {
      return `Value must be no more than ${this.max}`;
    }

    return '';
  }

  render() {
    const showError = this.error || this.hasValidationError;
    const displayHintText = showError && this.hasValidationError ? this.getValidationErrorMessage() : this.hintText;

    const inputFieldClasses = {
      'input-field': true,
      'input-hover': this.internalState === 'hover' || this.isHovered,
      'input-focused': this.internalState === 'focused' || this.isFocused,
      'input-error': showError,
      'input-disabled': this.disabled,
      'input-icon-leading': this.showLeadingIcon,
    };

    const textColorClass = this.currentValue !== null && this.currentValue !== undefined ? 'text-filled' : 'text-placeholder';
    const wrapperStyle = this.width ? { width: this.width } : {};

    const isDecrementDisabled = this.disabled || (this.min !== null && this.currentValue !== null && this.currentValue <= this.min);
    const isIncrementDisabled = this.disabled || (this.max !== null && this.currentValue !== null && this.currentValue >= this.max);

    return (
      <div class="input-wrapper" style={wrapperStyle}>
        <bh-label
          label={this.showLabel ? this.label : ''}
          required={this.required}
          showHelpIcon={this.showHelpIcon}
          helpIconTooltip={this.helpIconTooltip}
          disabled={this.disabled}
          onBhHelpClick={this.handleHelpIconClick}
        >
          <div
            class={inputFieldClasses}
            onMouseEnter={this.handleMouseEnter}
            onMouseLeave={this.handleMouseLeave}
          >
            <div class="input-content">
              {this.showLeadingIcon && (
                <span class="leading-icon material-symbols-outlined">
                  {this.leadingIcon}
                </span>
              )}
              {this.prefixText && <span class="prefix-text">{this.prefixText}</span>}
              <input
                ref={(el) => (this.inputEl = el)}
                type="number"
                name={this.name}
                value={this.currentValue ?? ''}
                placeholder={this.placeholder}
                disabled={this.disabled}
                required={this.required}
                class={`input-element ${textColorClass}`}
                onInput={this.handleInputChange}
                onFocus={this.handleInputFocus}
                onBlur={this.handleInputBlur}
                min={this.min ?? undefined}
                max={this.max ?? undefined}
                step={this.step}
              />
              {this.suffixText && <span class="suffix-text">{this.suffixText}</span>}
              <bh-button-icon
                hierarchy="tertiary"
                size="xs"
                iconName="remove"
                disabled={isDecrementDisabled}
                onClick={this.decrement}
                aria-label="Decrease value"
              />
              <bh-button-icon
                hierarchy="tertiary"
                size="xs"
                iconName="add"
                disabled={isIncrementDisabled}
                onClick={this.increment}
                aria-label="Increase value"
              />
            </div>
          </div>
        </bh-label>
        {this.showHintText && displayHintText && (
          <div class={{ 'hint-text': true, 'hint-error': showError }}>
            {displayHintText}
          </div>
        )}
      </div>
    );
  }
}
